@font-face {
  font-family: 'fontello';
  src: url('/etc/fonts/fontello/fontello.eot?62764575');
  src: url('/etc/fonts/fontello/fontello.eot?62764575#iefix') format('embedded-opentype'),
       url('/etc/fonts/fontello/fontello.woff2?62764575') format('woff2'),
       url('/etc/fonts/fontello/fontello.woff?62764575') format('woff'),
       url('/etc/fonts/fontello/fontello.ttf?62764575') format('truetype'),
       url('/etc/fonts/fontello/fontello.svg?62764575#fontello') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="icon-"]:before, [class*=" icon-"]:before {
  font-family: "fontello";
  font-style: normal;
  font-weight: normal;
  speak: none;

  display: inline-block;
  text-decoration: inherit;
  width: 1em;
  margin-right: .2em;
  text-align: center;

  font-variant: normal;
  text-transform: none;
  margin-left: .2em;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

}

.icon-mail:before { content: '\e800'; }
.icon-heart:before { content: '\e801'; }
.icon-lock:before { content: '\e802'; }
.icon-calendar:before { content: '\e803'; }
.icon-git:before { content: '\f09b'; }
.icon-location:before { content: '\f124'; }
.icon-link:before { content: '\f14c'; }
.icon-birthday:before { content: '\f1fd'; }
.icon-grab:before { content: '\f255'; }
.icon-spock:before { content: '\f259'; }
.icon-twitter:before { content: '\f304'; }
.icon-linkedin:before { content: '\f30c'; }
